<template>
	<div class="sub-container question">
		<div class="page-title center">
			<h2>1:1 문의</h2>
		</div>

		<ul class="tb" id="notice-list">
			<li class="tb-header">
				<span class="category">카테고리</span>
				<span>관련상품 및 제목</span>
				<span class="date">게시일</span>
			</li>
			<!-- 			<li
				class="tb-header flex marB-10"
				v-for="boardItem in boardList"
				:key="boardItem._id"
				:item="boardItem"
			> -->
			<ul v-for="(questionItem, index) in questionList" :key="questionItem._id">
				<router-link
					:to="{ name: 'QuestionDetail', params: { id: questionItem.id } }"
				>
					<li class="tb-body">
						<span class="category">{{
							showEnumAsString(questionItem.categoryId)
						}}</span>
						<div class="order-item">
							<span class="item-title"></span>
							<span class="question-title"
								>{{ questionItem.title }}({{ countList[index] }})</span
							>
						</div>
						<span class="date">{{
							questionItem.createDate && questionItem.createDate.substr(0, 10)
						}}</span>
					</li>
				</router-link>
			</ul>
		</ul>
		<LoadingSpinner :loading="isLoading" />

		<div class="botton-box">
			<Pagination
				v-if="totalPage !== -1"
				:listRowCt="15"
				:pageLinkCt="5"
				:totalCount="totalPage"
				:url="`api/user/question/all/hacho2011`"
			/>
			<button class="btn write-btn" @click="goto('/question-form')">
				글쓰기
			</button>
		</div>
	</div>
</template>

<script>
import Pagination from '@/components/common/Pagination';
import { fetchQuestions, fetchQuestionsTotalCount } from '@/api/question';
import { mapGetters } from 'vuex';
import { enumToString } from '@/utils/enumToString';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	created() {
		this.createdId = this.getIdentity;
		let page = this.$route.params.page - 1 || 0;
		this.fetchQuestions(this.createdId, page);
		this.fetchQuestionsTotalCount();
	},
	components: { LoadingSpinner, Pagination },
	computed: { ...mapGetters(['getIdentity']) },
	data() {
		return {
			totalPage: -1,
			isLoading: false,
			questionList: [
				{ categoryId: 'AC', title: '제목', date: '게시일', id: 1 },
			],
			countList: [2],
			cratedId: '',
		};
	},
	methods: {
		async fetchQuestions(created_id, page) {
			this.isLoading = true;
			try {
				const { data } = await fetchQuestions(created_id, page);
				this.questionList = data.questionList;
				this.countList = data.countList;
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
		goto(url) {
			this.$router.push({ path: url });
		},
		showEnumAsString(enumId) {
			return enumToString(enumId);
		},
		async fetchQuestionsTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchQuestionsTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchQuestions(this.createdId, this.currentPage - 1);
			}
		},
	},
};
</script>

<style lang="scss" scoped>
.sub-container.question {
	width: 700px;
	.tb {
		margin-bottom: 20px;

		.tb-header {
			border-top: 1px solid #e6e6e6;
			border-bottom: 1px solid #e6e6e6;
			height: 70px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			text-align: center;
			font-weight: 700;
			.category,
			.date {
				width: 120px;
			}
		}

		.tb-body {
			border-bottom: 1px solid #e6e6e6;
			height: 90px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			text-align: center;

			.category,
			.date {
				width: 120px;
			}
			.order-item {
				.item-title {
					display: block;
					color: #8c8c8c;
					text-align: center;
				}
				.question-title {
					display: block;
					text-align: center;
				}
			}
		}
	}

	.botton-box {
		display: flex;
		justify-content: space-between;
	}

	.write-btn {
	}
}
</style>
